import React, {useState} from "react";
import {Input} from './Input'
import {Radio} from './Radio'
const Search=(props)=> {
  const {
    change = Function.prototype,
  }=props
  const [search, setSearch]= useState('')
  const [type, setType]= useState('all')

  const inputHandler = (e) => {
    if (e.key === "Enter") {
      change(search, type);
    }
  };
  const handleFilter = (e) => {
    setType(e.target.dataset.type)
    change(search, e.target.dataset.type)
  };
  const ChangeValue=(e)=> setSearch(e.target.value);
  const clickHandler=(e)=> {
    // setSearch(e.target.value)
    change(search, type)
  }
  const Checking=(str)=>{
    return type === str ? true: false;
  }
    return (
      <div>
        <Input keyHandler={inputHandler} search={search} change={ChangeValue} click={clickHandler}/>
        <Radio filter={handleFilter} checking={Checking}  />
      </div>
    );
  }
  
export { Search };
